// tslint:disable-next-line:max-line-length
import { Component, OnInit, SimpleChanges, Output, EventEmitter } from '@angular/core';
import { MetadataInfoService } from '../../metadatainfo.service';
import { Metadata4RefDto, MetadataPackageHeader } from '../../../metadata4refdto';
import { NotifyService } from '@farris/ui-notify';
import { TranslateService } from '@ngx-translate/core';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-metadatainfo',
  templateUrl: './metadatainfo.component.html',
  styleUrls: ['./metadatainfo.component.css']
})
export class MetadataInfoComponent implements OnInit {
  boId: string;
  OK=this.transform('OK');
  cancel=this.transform('Cancel');
  @Output() selectedMetadata = new EventEmitter<any>();
  @Output() closeModalEmitter: EventEmitter<any>;
  constructor(private metadataInfoService: MetadataInfoService,private notifyService:NotifyService,  private translate: TranslateService) {
    this.closeModalEmitter = new EventEmitter<any>();
  }
  ngOnInit(): void {
  }

  transform(key: string) {
    return this.translate.instant(key);
  }

  LoadMetadata() {
    if(this.metadataInfoService.metadatas==null||this.metadataInfoService.metadatas.length<=0){
      this.notifyService.warning(this.transform('PleaseChooseData'));
      return;
    }
    const Id = this.metadataInfoService.metadatas;
    this.selectedMetadata.emit(this.metadataInfoService.metadatas);
    this.hideModal();
    // æ¸ç©ºç¼å­
    this.metadataInfoService.clearMetadataRT();
  }

  hideModal(): void {
    this.closeModalEmitter.emit();
  }

  onSelectedChange(bo: any) {
    if(bo.id==='gscloud'){
      this.boId='';
    }else{
      this.boId = bo.id;
    }
  }
}
