import { Component,  OnInit, SimpleChange, OnChanges, Input, Output, EventEmitter } from '@angular/core';
import { MetadataBaseInfo } from '../../metadatabaseinfo';
import { MetadataInfoService } from '../../metadatainfo.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-metadatainfolist',
  templateUrl: './metadatainfolist.component.html',
  styleUrls: ['./metadatainfolist.component.css']
})
// tslint:disable-next-line:component-class-suffix
export class MetadataInfoList implements OnInit, OnChanges {
  @Input() boId: string;
  allType = { text: this.transform('SelectAll'), value: 'All' };
  selectedType = this.allType;
  typeList: Array<{ text: string, value: string }> = [
    this.allType
  ];
  typeListReadonly = false;
  searchText = '';
  searchTextForDup = '';
  Code=this.transform('Code');
  data: MetadataBaseInfo[];
  dataCache: any[] = [];
  columns =  [];

  constructor(private metadataService: MetadataInfoService/**/,  private translate: TranslateService) {
    this.boId = 'all';
  }

  ngOnInit() {
    this.columns=[
      { field: 'nameSpace', width: 250, title: this.transform('NameSpace')  },
      { field: 'code', width: 200, title: this.transform('Code')},
      { field: 'name', width: 200, title: this.transform('Name')},
      { field: 'type', width: 150, title: this.transform('Type')}   
    ];
    // è·åææçåæ°æ®ç±»ååè¡¨
    this.metadataService.GetMetadataTypeList().subscribe(typeList => {
      if (typeList) {
        typeList.forEach(type => {
          this.typeList.push({ text: type.typeName, value: type.typeCode });
        });
      }
    });
    // åå§åæ°æ®
    this.getData();
  }
  clear() {
    this.data = [];
  }

  // tslint:disable-next-line:use-life-cycle-interface
  ngOnChanges(changes: { [propKey: string]: SimpleChange }) {
    if (this.searchText) {
      this.onSearchTextChanged();
      return;
    }
    for (const key in changes) {
      if (changes.hasOwnProperty(key)) {
        const changedProp = changes[key];
        const currentValue = changedProp.currentValue;
        this.boId=currentValue;
        if(this.dataCache){
          let dtos=[];
          dtos=this.filterByBoId(this.dataCache);
          dtos=this.filterByType(dtos);
          dtos=this.filterBySearchText(dtos,this.searchText.trim());
          this.data=dtos;
        }        
      }
    }
  }

  transform(key: string) {
    return this.translate.instant(key);
  }

  onSearchTextChanged() {
    if (!this.searchText) { this.searchText = ''; }
    const searchTextTrim = this.searchText.trim();
    console.log(this.searchText);
    this.filterData(searchTextTrim);
  }

  typeSelectedChanged(mdSeleted) {
    if (!mdSeleted) {
      mdSeleted = this.selectedType;
    } else {
      this.selectedType = mdSeleted;
    }
    if (this.searchText) {
      this.onSearchTextChanged();
      return;
    }
    if (mdSeleted) {
      let dtos= [];
      dtos=this.filterByBoId(this.dataCache);
      dtos=this.filterByType(dtos);
      this.data=dtos;
    }
  }

  goSearch() {
  }

  private getData() {
    const metadatadto = [];
    this.metadataService.GetMetadataRTList('').subscribe(metadataList => {
      if (metadataList) {
        this.metadataService.GetMetadataTypeList().subscribe(typeList => {
          if (typeList) {
            metadataList.forEach(item => {
              const dto = typeList.find(type => type.typeCode === item.metadata.type);
              if (dto) {
                item.metadata.type = dto.typeName;
              }
              metadatadto.push(item.metadata);
            });
          } else {
            metadataList.forEach(item => {
              metadatadto.push(item.metadata);
            });
          }
          this.data = metadatadto;
          this.dataCache = metadatadto;
        });
      }
    });
  }

  private filterData(filter: string) {
    let dtos=[];
    dtos=this.filterByBoId(this.dataCache);
    dtos=this.filterByType(dtos);
    dtos=this.filterBySearchText(dtos,filter);
    this.data=dtos;
  }

  private filterByType(datas : MetadataBaseInfo[]){
    let dtos= [];
    if (this.selectedType.value === 'All') {
        dtos=datas;
    } else{
      datas.forEach(data => {
        this.data
        // tslint:disable-next-line:max-line-length
        if (data.type === this.selectedType.text) {
          dtos.push(data);
        }
      });
    }
    return dtos;
  }
  private filterByBoId(datas : MetadataBaseInfo[]){
    let dtos = [];
    if (!this.boId) {
        dtos=datas;
    } else{
      datas.forEach(data => {
        // tslint:disable-next-line:max-line-length
        if (data.bizobjectID === this.boId) {
          dtos.push(data);
        }
      });
    }
    return dtos;
  }
  private filterBySearchText(datas : MetadataBaseInfo[],text:string){
    let dtos = [];
    if (!text) {
        dtos=datas;
    } else{
      datas.forEach(data => {
        if (data.code.toLowerCase().indexOf(text.toLowerCase()) > -1 || data.name.toLowerCase().indexOf(text.toLowerCase()) > -1) {
          dtos.push(data);
        }
      });
    }
    return dtos;
  }

  // éä¸­åæ°æ®åçäºä»¶å¤ç
  onSelectionChange(event) {
    if (event.selectedRows.length > 0) {
      this.metadataService.clearMetadataRT();
      const metadata = {
        ID: event.selectedRows[0].dataItem.id,
        RelativePath: event.selectedRows[0].dataItem.relativePath,
        FileName: event.selectedRows[0].dataItem.fileName
      };
      this.metadataService.addMetadataRT(metadata);
    }
  }
  onPageSizeChanged($event) {
    console.log('PageSize Changed:', $event);
  }

  onPageChanged($event) {
    console.log('PageNumber Changed:', $event);
  }

  checkedChange(event){
    if(event!=null&&event.length>0){
      this.metadataService.clearMetadataRT();
      event.forEach(element => {
        const metadata = {
          ID: element.data.id,
          Name: element.data.name,
          Code: element.data.code,
          Type: element.data.type,
          NameSpace: element.data.nameSpace
        };
        this.metadataService.addMetadataRT(metadata);
      });
    }
  }
}
