import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { GSPMetadataRTService4BizType } from '../metadata.rtservice4biztype';

@Injectable()
export class MetadataInfoService {
  // tslint:disable-next-line:variable-name
  private BusinessObject_Base_Path = '/api/dev/main/v1.0/business-objects';
  constructor(private http: HttpClient, private gspMetadataService: GSPMetadataRTService4BizType) { }

  metadatas = [];
  addMetadataRT(metadata) {
    this.metadatas.push(metadata);
  }
  clearMetadataRT() {
    this.metadatas = [];
  }

  // è·ååæº
  getBusinessObject(obId: string): Observable<any> {
    const url = this.BusinessObject_Base_Path + '/' + obId;
    return this.http.get(url);
  }

  GetMetadataTypeList(): Observable<any> {
    return this.gspMetadataService.GetMetadataTypeList();
  }

  GetMetadataRTList(typeList: string): Observable<any> {
    return this.gspMetadataService.GetAllOriginMdRTListWithType(typeList);
  }

  // è·æ ¹æ®Idåæå®åæ°æ®
  GetMetadataRT(metadataID: string): Observable<any> {
    return this.gspMetadataService.GetMetadataRT(metadataID);
  }
}
